@import url('https://fonts.googleapis.com/css?family=Karla:400,400i');

.form {
    margin: 3rem 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-item-align: center;
        align-self: center;
}

.form-input {
    outline: none;
    border: none;
    padding: 1rem 0rem;
    background: transparent;
    font-size: 3rem;
    border-bottom: 4px solid #e9abb6;
    margin-right: 16px;
    color: #e9abb6;
    font-family: 'Karla', sans-serif;
}

.button {
    outline: none;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    padding: 1rem 2rem;
    background: #e9abb6;
    font-size: 3rem;
    color: #fff;
    font-family: 'Karla', sans-serif;
    font-style: italic;
    text-transform: uppercase;
    font-weight: lighter;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 5px solid transparent;
}

.button:focus,
.button:hover {
    background-color: #bd91cc;
    -webkit-transition: background-color .15s linear;
    -o-transition: background-color .15s linear;
    transition: background-color .15s linear;
}

