    color: #bd91cc;
    flex-direction: column;
    font-size: 1.5rem;
    font-weight: light;
    opacity: .4;
    margin: 20px;
}*/

@media only screen and (max-width: 768px) {
    .form {
        display: block;
        margin: 0 auto;
        text-align: center;
    }

    .button,
    .form-input {
        margin: 1rem 0;
        padding: 0.5rem 1rem;
        font-size: 1.7rem;
    }
}


